.container {
  max-width: 1200px;
  border: solid 1px #05065a;
  margin: 0 auto;
  padding: 0 20px;
  color: #b8d9a5;

  h2 {
    font-size: 30px;
    margin: 0;

    @media (min-width: 600px) {
      font-size: 60px;
    }
  }
}

.hideOnMobile {
  display: none;

  @media (min-width: 600px) {
    display: inline-block;
  }
}
